import View, { ViewProps} from '@components/editor/View';
import React from 'react';
import ace from 'ace-builds';
import { Box } from '@mui/material';

export interface CodeViewProps extends ViewProps {}

export default function CodeView({ ...viewProps }: CodeViewProps): React.JSX.Element {

  React.useEffect(initializationEffect, []);

  return <View {...viewProps}>
    <Box id='editor' width='100%' height='100%' />
  </View>;
  
  function initializationEffect(): void {
    var editor = ace.edit('editor');
  }
  
}