'use client'

import di from '@/DependencyInjection';
import '@/User';
import { UserServiceId } from '@interfaces/IUser';
import Page, { PageProps } from '@root/components/page/Page';
import { usePathname, useRouter } from 'next/navigation';
import React from 'react';

export interface AuthenticatedPageProps extends PageProps {}

export default function AuthenticatedPage({ navBarChildren, ...pageProps }: AuthenticatedPageProps): React.JSX.Element {
  const user = di.get(UserServiceId);
  if (!user.account) {
    const router = useRouter();
    const pathname = usePathname();
    router.replace(`/login?redirect=${pathname}`);
    return <React.Fragment />;
  }

  return <Page {...pageProps} />;
}
